<template>
  <div @focus="edit = true" @focusout="edit = false" tabindex="-1" class="cell-select-static">
    <div @focus="edit = true" class="cell-select-static">
      {{ value }}
    </div>
    <div class="cell-select-dropdown" v-if="edit">
        <div class="cell-select-dropdown__item" v-for="option in options" :key="option.id" @click="$emit('edit', option.id)">{{ option.name }}</div>
    </div>
</div>
</template>

<script>
export default {
    data() {
        return {
            edit: false,
            select: "",
        }
    },
    methods: {
        async focus() {
            this.edit = true;
            await new Promise((resolve) => setTimeout(resolve, 100));
        }
    },
    mounted() {
        this.select = this.value
    },
    props: {
        value: {
            type: String,
            default: "",
            required: false,
        },
        options: {
            type: Array,
            default: () => [],
            required: true,
        }
    }
};
</script>

<style lang="scss">
    .cell-select {
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        padding: 2px 20px 2px 5px;
        font-family: 'Rubik';
        font-size: inherit;
        &::after {
            content: '';
            position: absolute;
            top: 50%;
            right: 5px;
            transform: translateY(-50%);
        }
        &-static {
            padding: 2px 20px 2px 5px;
        }
        &:focus {
            outline: none;
        }
        &-dropdown {
            background: white;
            color: black;
            z-index: 5;
            left: 0;
            position: absolute;
            width: fit-content;
            min-width: 100%;
            top: 100%;
            max-height: 200px;
            overflow-y: auto;
            overflow-x: hidden;
            &__item {
                padding: 5px;
                cursor: pointer;
                &:hover {
                    background-color: rgba(0, 0, 30, 0.1);
                }
            }
        }
    }
</style>