<template>
  <div class="members-list">
    <table>
      <tr>
        <th></th>
        <th></th>
        <th></th>
        <th> (   )</th>
      </tr>
      <tr
        class="squad-positions__item"
        v-for="player in getPlayers"
        :key="player.id"
      >
        <td class="nopadding">
          <CellInput :value="player && player.name.replace('_', ' ')" @edit="updateName(player.id, ...arguments)" />
        </td>
        <td class="nopadding">
          <CellSelect :value="getRank(player.rankId) && getRank(player.rankId).name" :options="getRanks" @edit="updateRank(player.id, ...arguments)"/>
        </td>
        <td class="nopadding" :style="{ 'background-color': getClist(getPosition(player.positionId) && getPosition(player.positionId).clist) + '66' }">
          <CellSelect :value="getPosition(player.positionId) && getPosition(player.positionId).name" :options="getPositions" @edit="updatePosition(player.id, ...arguments)"/>
        </td>
        <td class="nopadding long-text">
          <CellInput :value="player && player.avatar" @edit="updateAvatar(player.id, ...arguments)" />
        </td>
        <td class="edit-buttons no-bg">
<!--           <div class="edit-buttons__item">
            <icon name="pen" class="icon" scale="1"></icon>
          </div> -->
          <div class="edit-buttons__item" @click="uninvite(player.id)">
            <icon name="times" class="icon icon_red" scale="1"></icon>
          </div>
        </td>
      </tr>
    </table>
    <UIButton title="+ " @click="addMember"/>
  </div>
</template>

<script>
import Icon from "vue-awesome/components/Icon";
import "vue-awesome/icons";
import UIButton from '../ui/UIButton.vue';
import CellInput from '../CellInput.vue';
import CellSelect from '../CellSelect.vue';
export default {
  components: {
    Icon,
    UIButton,
    CellInput,
    CellSelect
  },
  
  computed: {
    getPositions() {
      return this.$store.getters["positions"].sort(
        (a, b) => b.priority - a.priority
      );
    },
    getRanks() {
      return this.$store.getters["policeRanks"].sort(
        (a, b) => a.id - b.id
      );
    },
    getPlayers() {
      return this.$store.getters["players"].filter(val => val.factionId === 1).sort((a, b) => b.rankId - a.rankId)
    }
  },
  methods: {
    getClist(id) {
      return this.$store.getters["getClistColor"](id);
    },
    getPosition(id) {
      return this.$store.getters["positionById"](id);
    },
    getSquad(id) {
        return this.$store.getters["squadById"](id)
    },
    getRank(id) {
        return this.$store.getters["getRank"](id)
    },
    uninvite(id) {
      this.$store.commit('updatePlayers', [id, {factionId: 0}])
    },
    addMember() {
      this.$store.commit('addPlayers', [{factionId: 1}])
    },
    updateName(id, value) {
      this.$store.commit('updatePlayers', [id, {name: value.replace(' ', '_')}])
    },
    updateAvatar(id, value) {
      this.$store.commit('updatePlayers', [id, {avatar: value}])
    },
    updateRank(id, value) {
      this.$store.commit('updatePlayers', [id, {rankId: value}])
    },
    updatePosition(id, value) {
      this.$store.commit('updatePlayers', [id, {positionId: value}])
    },
  }

};
</script>

<style lang="scss">
  .edit-buttons {
    display: flex;
    align-items: center;
    gap: 10px;
    &__item {
      cursor: pointer;
      display: flex;
    align-items: center;
    justify-content: center;
      .icon {
        fill: rgba(255, 255, 255, 0.9);
        &_red {
          fill: rgba(255, 0, 0, 0.9);
        }
      }
    }
  }

  .members-list {
    table {
      margin-bottom: 20px;
    }
  }
</style>